import { Component, OnInit } from '@angular/core';
import { ExchangeService } from '../services/exchange.service';
import { AppExchange } from '../model/exchange.model';
import { AppTicker } from '../model/ticker.model';

@Component({
  selector: 'app-brief',
  templateUrl: './brief.component.html',
  styleUrls: ['./brief.component.css']
})
export class BriefComponent implements OnInit {

  tickers: AppTicker[];
  exchange: AppExchange;
  favoritePairs: any[] = [];
  favoriteTickers: any[] = [];

  constructor(public exchangeService: ExchangeService) {

  }

  async ngOnInit() {
    this.loadExchangeInfo();
    this.tickers = await this.loadTickers();
    this.restoreFavoritePairs();
  }

  async loadTickers(){
    return this.exchangeService.getTickers();
  }

  async loadExchangeInfo(){
    this.exchange = await this.exchangeService.getExchange();
  }

  restoreFavoritePairs(){
    this.favoriteTickers = [];
    let pairs = localStorage.getItem('pairs');
    if (pairs){
      this.favoritePairs = JSON.parse(pairs);
      this.favoritePairs.forEach(pair => {
        let appTicker = this.tickers.find(ticker => ticker.exchange == pair.exchange && ticker.symbol == pair.symbol);
        appTicker.isFavorite = true;
        if (appTicker) this.favoriteTickers.push(appTicker);
      });
    }
    console.log(this.favoriteTickers);
  }

  tickerClick(ticker:AppTicker){
    this.saveOrRemoveFavoritePair(ticker);
  }

  saveOrRemoveFavoritePair(ticker:AppTicker){
    if (ticker.isFavorite){
      let existPairIndex = this.favoritePairs.findIndex(item => item.exchange === ticker.exchange && item.symbol === ticker.symbol);
      if (existPairIndex > -1){
        this.favoritePairs.splice(existPairIndex, 1);
      }
      this.favoriteTickers = this.favoriteTickers.filter(item => item !== ticker);
    }
    else{
      let pair = {exchange: 'exmo', 'symbol': ticker.symbol};
      this.favoritePairs.push(pair);
      this.favoriteTickers.push(ticker);
    }
    ticker.isFavorite = !ticker.isFavorite;
    localStorage.setItem('pairs', JSON.stringify(this.favoritePairs));
  }

}
