import { Injectable } from '@angular/core';
import * as ccxt from 'ccxt';

import { AppExchange } from '../model/exchange.model';
import { AppTicker } from '../model/ticker.model';

@Injectable({
  providedIn: 'root'
})
export class ExchangeService {

  private _exchangeId:string = 'exmo';
  private _exchange: ccxt.Exchange;

  public appExchange: AppExchange;
  public appTickers: AppTicker[];

  constructor() {
  }

  getExchange(){
    this._exchange = new ccxt[this._exchangeId]();
    this.appExchange = { name: this._exchange.name };
    return this.appExchange;
  }

  async getTickers() {
    if (!this._exchange) this.getExchange();
    let tickers = <any>await this._exchange.fetchTickers();
    console.log(tickers);
    this.appTickers = [];
    for(let pair in tickers){
      this.appTickers.push({
        exchange: this._exchangeId,
        symbol: tickers[pair].symbol,
        price: tickers[pair].last,
        bid: tickers[pair].bid,
        ask: tickers[pair].ask,
        spread: this.calcSpread(tickers[pair].bid, tickers[pair].ask),
        change: this.calcChangePercent(tickers[pair].average, tickers[pair].last),
        volume: tickers[pair].baseVolume,
        trend: 'bullish',
        isFavorite: false
      })
    }
    return this.appTickers;
  }

  calcChangePercent(averagePrice: number, lastPrice: number): string{
    let value = ((lastPrice/averagePrice) - 1) * 100;
    let result = value > 0 ? '+' + value.toFixed(2) : value.toFixed(2);
    return result;
  }

  calcSpread(bid: number, ask: number): string{
    let result = ((ask - bid) / ask);
    return result.toFixed(4);
  }
}
